.tabs {
	position: relative;
	width: 100%;

	&__inner {
		position: absolute;
		left: -1000%;
		z-index: 0;
		opacity: 0;
		top: 0;
		width: 100%;
		transition: opacity 500ms;

		&.active {
			left: 0;
			opacity: 1;
			z-index: 1;
			position: relative;
		}
	}
	@media (--phone) {
		&_detail {
			margin-bottom: 30px;
		}
	}
}
